___CSS_LOADER_EXPORT___.push([module.id, ".full-width,.my-table{width:100%}.my-table,.my-td{border:1px solid #ccc}.my-td{padding:2px}.opened{background-color:#02fffb}.row-centered{text-align:center}.col-centered{display:inline-block;float:none;margin-right:-4px;text-align:left}.pos{position:top;top:240px}.v-enter-active,.v-leave-active{transition:opacity .5s ease}.v-enter-from,.v-leave-to{opacity:0}.bounce-enter-active{animation:bounce-in .5s}.bounce-leave-active{animation:bounce-in .5s reverse}@keyframes bounce-in{0%{transform:scale(0)}50%{transform:scale(1.1)}to{transform:scale(1)}}", "",{"version":3,"sources":["webpack://./resources/js/Pages/SummaryOfRating/MonthlyRating.vue"],"names":[],"mappings":"AAuJA,sBAJI,UAOJ,CAEA,iBAHI,qBAMJ,CAHA,OACI,WAEJ,CAEA,QACI,wBACJ,CAGA,cACI,iBACJ,CAEA,cACI,oBAAqB,CACrB,UAAW,CAEX,iBAAkB,CADlB,eAEJ,CAEA,KACI,YAAa,CACb,SACJ,CAGA,gCAEI,2BACJ,CAEA,0BAEI,SACJ,CAGA,qBACI,uBACJ,CAEA,qBACI,+BACJ,CAEA,qBACI,GACI,kBACJ,CAEA,IACI,oBACJ,CAEA,GACI,kBACJ,CAGJ","sourcesContent":["<template>\r\n\r\n    <Head>\r\n        <title>Home</title>\r\n    </Head>\r\n    <div class=\"row gap-20 masonry pos-r\">\r\n        <div class=\"peers fxw-nw jc-sb ai-c\">\r\n            <h3>Employee's Monthly Rating - {{ month }}</h3>\r\n            <div class=\"peers\">\r\n                <div class=\"peer mR-10\">\r\n\r\n                </div>\r\n                <div class=\"peer\">\r\n\r\n                    <button class=\"btn btn-primary btn-sm mL-2 text-white\" @click=\"printSubmit1\">Print Summary\r\n                    </button>\r\n\r\n                </div>\r\n                <div class=\"peer\">\r\n\r\n                </div>\r\n            </div>\r\n\r\n            <Link :href=\"'/summary-rating'\">\r\n            <svg xmlns=\"http://www.w3.org/2000/svg\" width=\"25\" height=\"25\" fill=\"currentColor\" class=\"bi bi-x-lg\"\r\n                viewBox=\"0 0 16 16\">\r\n                <path fill-rule=\"evenodd\"\r\n                    d=\"M13.854 2.146a.5.5 0 0 1 0 .708l-11 11a.5.5 0 0 1-.708-.708l11-11a.5.5 0 0 1 .708 0Z\" />\r\n                <path fill-rule=\"evenodd\"\r\n                    d=\"M2.146 2.146a.5.5 0 0 0 0 .708l11 11a.5.5 0 0 0 .708-.708l-11-11a.5.5 0 0 0-.708 0Z\" />\r\n            </svg>\r\n            </Link>\r\n        </div>\r\n\r\n        <div class=\"masonry-sizer col-md-6\"></div>\r\n        <div class=\"masonry-item w-100\">\r\n            <div class=\"row gap-20\"></div>\r\n            <div class=\"bgc-white p-20 bd\">\r\n                <div class=\"table-responsive\">\r\n                    <table class=\"table table-sm table-bordered border-dark table-hover\">\r\n                        <thead>\r\n                            <tr style=\"background-color: #B7DEE8;\" class=\"text-center table-bordered\">\r\n                                <th style=\"width: 5%;\" rowspan=\"2\" colspan=\"1\">Full Name</th>\r\n                                <th style=\"width: 20%;\" colspan=\"4\">Rating</th>\r\n                            </tr>\r\n                            <tr style=\"background-color: #B7DEE8;\" class=\"text-center\">\r\n                                <th style=\"width: 5%;\">Numerical Rating</th>\r\n                                <th style=\"width: 5%;\">Adjectival Rating</th>\r\n                            </tr>\r\n                            <tr>\r\n\r\n                            </tr>\r\n                        </thead>\r\n                        <tbody>\r\n\r\n                            <template v-for=\"(dat, index) in data\" :key=\"index\">\r\n                                <tr>\r\n                                    <td>{{ dat.Fullname }}</td>\r\n                                    <td>{{ dat.numericalRating }}</td>\r\n                                    <td>{{ dat.adjectivalRating }}</td>\r\n                                </tr>\r\n                            </template>\r\n                        </tbody>\r\n                    </table>\r\n                </div>\r\n\r\n            </div>\r\n        </div>\r\n        <Modal v-if=\"displayModal\" @close-modal-event=\"hideModal\">\r\n            <!-- {{ my_link }} -->\r\n            <div class=\"d-flex justify-content-center\">\r\n                <iframe :src=\"my_link\" style=\"width:100%; height:450px\" />\r\n            </div>\r\n        </Modal>\r\n\r\n\r\n    </div>\r\n</template>\r\n<script>\r\n\r\nimport { useForm } from \"@inertiajs/inertia-vue3\";\r\nimport Modal from \"@/Shared/PrintModal\";\r\nimport Modals from \"@/Shared/Modal\"\r\nexport default {\r\n    props: {\r\n        auth: Object,\r\n        emp_code: String,\r\n        data: Object,\r\n        month: String,\r\n        year: String,\r\n        office: String,\r\n        month_data: Object,\r\n        dept: Object,\r\n        pgHead: String,\r\n        sem_id: String,\r\n        status: String\r\n    },\r\n    data() {\r\n        return {\r\n            displayModal: false,\r\n            my_link: \"\",\r\n            form: useForm({\r\n                remarks: \"\",\r\n                remarks_id: \"\",\r\n                year: \"\",\r\n                month: \"\",\r\n                idIPCR: \"\",\r\n                idSemestral: \"\",\r\n                emp_code: \"\",\r\n            })\r\n\r\n        }\r\n    },\r\n    components: {\r\n       Modal, Modals,\r\n    },\r\n\r\n    methods: {\r\n        printSubmit1() {\r\n\r\n            this.my_link = this.viewlink1(this.year, this.month, this.office);\r\n            this.showModal1();\r\n\r\n            console.log(this.my_link)\r\n\r\n        },\r\n        viewlink1(year, month, office) {\r\n            var linkt = \"http://\";\r\n            var jasper_ip = this.jasper_ip;\r\n            var jasper_link = 'jasperserver/flow.html?pp=u%3DJamshasadid%7Cr%3DManager%7Co%3DEMEA%2CSales%7Cpa1%3DSweden&_flowId=viewReportFlow&_flowId=viewReportFlow&ParentFolderUri=%2Freports%2FIPCR&reportUnit=%2Freports%2FIPCR%2FMonthlyRating&standAlone=true&decorate=no&output=pdf';\r\n            var params = '&year=' + year + '&month=' + month + '&department_code=' + office;\r\n            var linkl = linkt + jasper_ip + jasper_link + params;\r\n\r\n            return linkl;\r\n        },\r\n        showModal1() {\r\n            this.displayModal = true;\r\n        },\r\n        hideModal() {\r\n            this.displayModal = false;\r\n        },\r\n    }\r\n};\r\n</script>\r\n<style>\r\n/***TABLE FULL WIDTH */\r\n.full-width {\r\n    width: 100%;\r\n}\r\n\r\n/**ACCORDION BEGIN*********************/\r\n.my-table {\r\n    width: 100%;\r\n    border: 1px solid #ccc;\r\n}\r\n\r\n.my-td {\r\n    padding: 2px;\r\n    border: 1px solid #ccc;\r\n}\r\n\r\n.opened {\r\n    background-color: rgb(2, 255, 251);\r\n}\r\n\r\n/**ACCORDION END*********************/\r\n.row-centered {\r\n    text-align: center;\r\n}\r\n\r\n.col-centered {\r\n    display: inline-block;\r\n    float: none;\r\n    text-align: left;\r\n    margin-right: -4px;\r\n}\r\n\r\n.pos {\r\n    position: top;\r\n    top: 240px;\r\n}\r\n\r\n/*TOGGLE FADE TRANSITION*/\r\n.v-enter-active,\r\n.v-leave-active {\r\n    transition: opacity 0.5s ease;\r\n}\r\n\r\n.v-enter-from,\r\n.v-leave-to {\r\n    opacity: 0;\r\n}\r\n\r\n/* transition */\r\n.bounce-enter-active {\r\n    animation: bounce-in 0.5s;\r\n}\r\n\r\n.bounce-leave-active {\r\n    animation: bounce-in 0.5s reverse;\r\n}\r\n\r\n@keyframes bounce-in {\r\n    0% {\r\n        transform: scale(0);\r\n    }\r\n\r\n    50% {\r\n        transform: scale(1.1);\r\n    }\r\n\r\n    100% {\r\n        transform: scale(1);\r\n    }\r\n\r\n\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
